import React from 'react'
import "./Contactform.css";
const Contactform = () => {
    return (
        <section className="form-area">
            <div className="formarea-wrapper">
                <form className="contact-form" action="POST">
                    <h4>
                        Get in touch with me
                    </h4>
                    <input type="text" id="fname" name="firstname" placeholder="Your name.." />
                    <input type="text" id="email" name="email" placeholder="Your email ..." />
                    <input type="number" id="number" name="number" placeholder="Your phone number...."/>
                    <textarea id="subject" name="subject" placeholder="Enter your message.."></textarea>
                    <input type="submit" value="Submit"/>
                </form>
            </div>
        </section>
    )
}

export default Contactform
